/**
 * StepProgressBar.js
 * Used to display the progress steps used in all categories
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Slider from '../../FormsNew/Slider/SliderNew';
import SliderNew from '../../FormsV3/Slider/SliderNew';

import { setInsuranceFormState, FORMSTATES } from '../../../actions/insuranceActions';

import './StepProgressBarNew.scss';

class StepProgressBarNew extends Component {
  constructor(props) {
    super(props);
    this.getStateList = this.getStateList.bind(this);
    this.renderDescription = this.renderDescription.bind(this);
  }

  getStateList() {
    const {
      stateList,
      history,
    } = this.props;

    return _.map(stateList, (state, index) => ({
      state: state.index,
      callback: (formState) => {
        const currIndex = _.findIndex(stateList, s => s.index === formState);
        history.go(-(currIndex - index));
      },
    }));
  }

  renderDescription() {
    const {
      formState,
      stateList,
      stateDescriptions,
      charged,
      history,
    } = this.props;

    try {
      const currIndex = _.findIndex(stateList, state => state.index === formState);
      const mainDesc = charged ? 'Receipt' : stateDescriptions[currIndex].main;
      const subDesc = stateDescriptions[currIndex].sub;

      if (!mainDesc && !subDesc) {
        return null;
      }

      return (
        <Col className="step-progress-description-container-new" xs={12}>
          {
            currIndex !== 0 ?
              <button
                className="back-button"
                onClick={() => {
                  history.goBack();
                }}
              >
                Back
              </button>
            :
              null
          }
          {
            mainDesc ?
              <p className="main">{mainDesc}</p>
            :
              null
          }
          {
            subDesc ?
              <p className="sub">{subDesc}</p>
            :
              null
          }
        </Col>
      );
    } catch (error) {
      return null;
    }
  }

  render() {
    const {
      formState,
      charged,
      inputList,
      maxWidth,
      className,
      formStates,
      v3Slider,
    } = this.props;

    const formStatesToUse = formStates || FORMSTATES;

    if (formState !== formStatesToUse.MAIN.index && !charged) {
      return (
        <Row className={`justify-content-center ${className}`} noGutters>
          <Col className="mx-auto product-slider-new" xs={12}>
            {
              v3Slider ?
                <SliderNew
                  isProductSlider
                  hideWidth={576}
                  maxWidth={maxWidth}
                  inputList={inputList}
                  formStateList={this.getStateList()}
                  formState={formState}
                />
              :
                <Slider
                  isProductSlider
                  hideWidth={576}
                  maxWidth={maxWidth}
                  inputList={inputList}
                  formStateList={this.getStateList()}
                  formState={formState}
                />
            }
          </Col>
          {this.renderDescription()}
        </Row>
      );
    }

    return null;
  }
}

StepProgressBarNew.defaultProps = {
  charged: null,
  maxWidth: '400px',
  className: '',
  formStates: null,
  v3Slider: false,
};

StepProgressBarNew.propTypes = {
  formState: PropTypes.number.isRequired,
  // setInsurFormState: PropTypes.func.isRequired,
  inputList: PropTypes.array.isRequired,
  stateList: PropTypes.array.isRequired,
  charged: PropTypes.bool,
  stateDescriptions: PropTypes.array.isRequired,
  maxWidth: PropTypes.string,
  history: PropTypes.object.isRequired,
  className: PropTypes.string,
  formStates: PropTypes.object,
  v3Slider: PropTypes.bool,
};

const mapDispatchToProps = dispatch => ({
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(StepProgressBarNew));
