/**
 * Slider.js
 * Selection with dynamic content
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import React, { Component, Fragment } from 'react';
import { Field, getFormValues } from 'redux-form';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import _ from 'lodash';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';
import DropDownV2 from '../DropDown/DropDown';

import './SliderNew.scss';

const marginTopDefault = 0; // 60;

const SLIDERSTATE = {
  ONIDLE: 0,
  ONDOWN: 1,
  ONUP: 2,
};

/**
 * Custom Selection Class
 */
class SliderNew extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const {
      storeForm,
      inputList,
      name,
      formState,
      formStateList,
    } = nextProps;

    const { activeIndex } = prevState;

    let indexToChange = null;
    if (storeForm) {
      const selectedTabString = storeForm[name];
      const selectedTabIndex = inputList.indexOf(selectedTabString);
      if (selectedTabIndex !== -1 && selectedTabIndex !== activeIndex) {
        indexToChange = selectedTabIndex;
      }
    }

    if (formState) {
      const formStateIndex = _.findIndex(formStateList, listState => listState.state === formState);
      if (formStateIndex !== activeIndex) {
        indexToChange = formStateIndex;
      }
    }

    if (indexToChange !== null) {
      const len = inputList.length - 1;
      const newWidth = indexToChange / len * 100;

      return ({
        activeIndex: indexToChange,
        sliderWidth: newWidth,
      });
    }

    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      sliderWidth: 0,
      sliderState: SLIDERSTATE.ONIDLE,
      activeIndex: 0,
      isUseDropDown: false,
      marginTop: marginTopDefault,
    };

    this.renderKnobs = this.renderKnobs.bind(this);
    this.renderSummary = this.renderSummary.bind(this);

    this.handleResize = this.handleResize.bind(this);

    this.handleSliderDown = this.handleSliderDown.bind(this);
    this.handleSliderMove = this.handleSliderMove.bind(this);
    this.handleSliderUp = this.handleSliderUp.bind(this);
  }

  componentDidMount() {
    document.addEventListener('mousemove', this.handleSliderMove);
    document.addEventListener('mouseup', this.handleSliderUp);
    window.addEventListener('resize', this.handleResize);

    const {
      storeForm,
      name,
      change,
      inputList,
      isProductSlider,
    } = this.props;

    if (!isProductSlider && !(storeForm && storeForm[name])) {
      change(name, inputList[0]);
    }

    this.handleResize();
  }

  componentWillUnmount() {
    document.removeEventListener('mousemove', this.handleSliderMove);
    document.removeEventListener('mouseup', this.handleSliderUp);
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize() {
    const { hideWidth, isProductSlider } = this.props;
    if (!isProductSlider && hideWidth > 0) {
      const isDrop = window.innerWidth <= hideWidth;
      this.setState({
        isUseDropDown: isDrop,
        marginTop: isDrop ? 0 : marginTopDefault,
      });
    }
  }

  handleSliderDown() {
    const { sliderState } = this.state;

    if (sliderState !== SLIDERSTATE.ONDOWN) {
      this.setState({
        sliderState: SLIDERSTATE.ONDOWN,
      });
    }
  }

  handleSliderMove(event) {
    const { sliderState } = this.state;

    if (sliderState === SLIDERSTATE.ONDOWN) {
      const rect = this.sliderStart.getBoundingClientRect();
      const position = Math.max(0, Math.min(event.clientX - rect.x - 5, rect.width)) / rect.width * 100;
      this.setState({
        sliderWidth: position,
      });
    }
  }

  handleSliderUp() {
    this.handleClick();
  }

  handleClick(index = null) {
    const { inputList, isProductSlider } = this.props;

    const { sliderWidth } = this.state;
    const len = inputList.length - 1;
    const indexToUse = this.handleProductSlider(index === null ? Math.round(sliderWidth / 100 * len) : index);
    const newWidth = indexToUse / len * 100;

    if (!isProductSlider) {
      const {
        name,
        change,
      } = this.props;

      change(name, inputList[indexToUse]);
    }

    this.setState({
      sliderState: SLIDERSTATE.IDLE,
      sliderWidth: newWidth,
      activeIndex: indexToUse,
    });

    if (inputList[indexToUse].callback) {
      inputList[indexToUse].callback();
    }
  }

  handleProductSlider(indexToUse) {
    const { formState, formStateList } = this.props;
    if (!formState) {
      return indexToUse;
    }

    const formStateIndex = _.findIndex(formStateList, listState => listState.state === formState);
    const isValidSelection = indexToUse < formStateIndex;
    if (isValidSelection && formStateList[indexToUse].callback) {
      formStateList[indexToUse].callback(formState);
    }

    return isValidSelection ? indexToUse : formStateIndex;
  }

  renderKnobs() {
    const { activeIndex } = this.state;
    const { inputList, isProductSlider, displayList } = this.props;

    const inputLength = inputList.length - 1;
    return _.map(inputList, (data, index) => {
      const leftOffset = index / inputLength * 100;
      const isProductPassedPage = isProductSlider && index <= activeIndex;
      let activeStyle = '';
      if (isProductPassedPage || (!isProductSlider && index === activeIndex)) {
        activeStyle = 'desc-active';
      }
      const sliderStyle = isProductPassedPage ? 'knob-done' : 'knob-default';
      const indexToUse = index;
      const dataToUse = displayList && displayList[index] ? displayList[index] : data;
      const descStyle = isProductSlider ? 'desc-bottom' : '';

      return (
        <button
          key={data}
          tabIndex={-1}
          className={`knob ${sliderStyle}`}
          style={{ left: `${leftOffset}%` }}
          onClick={(e) => { e.preventDefault(); this.handleClick(indexToUse); }}
          onKeyDown={() => {}}
        >
          <p className={`${activeStyle} desc ${descStyle}`}>
            {dataToUse}
          </p>
        </button>
      );
    });
  }

  renderSummary() {
    const {
      activeIndex,
    } = this.state;

    const {
      inputList,
      prefix,
      suffix,
      isProductSlider,
      displayList,
    } = this.props;

    if (!isProductSlider) {
      const value = inputList[activeIndex];
      const valueToUse = displayList && displayList[activeIndex] ? displayList[activeIndex] : value;

      return (
        <div className="slider-summary-container-new">
          {
            prefix ?
              <span className="prefix-suffix">{`${prefix} `}</span>
            :
              null
          }
          <span className="value">{valueToUse}</span>
          {
            suffix ?
              <span className="prefix-suffix">{` ${suffix}`}</span>
            :
              null
          }
        </div>
      );
    }

    return null;
  }

  render() {
    const {
      sliderWidth,
      isUseDropDown,
      marginTop,
    } = this.state;

    const {
      name,
      desc,
      form,
      change,
      inputList,
      extraInfo,
      isProductSlider,
      maxWidth,
      errorString,
    } = this.props;

    const knobColor1 = isProductSlider ? 'white-knob' : 'color-knob';
    const knobColor2 = isProductSlider ? 'color-knob' : 'white-knob';

    return (
      <FormInputWrapper
        desc={desc}
        ref={node => (this.formParent = node)}
        extraInfo={extraInfo}
        errorString={errorString}
      >
        <Fragment>
          {this.renderSummary()}
          <div
            className="slider-container-v3"
            style={{ marginTop, maxWidth }}
          >
            {
              isUseDropDown ?
                <DropDownV2
                  form={form}
                  change={change}
                  name={name}
                  placeholderString="Enter or select your destinations"
                  dropList={inputList}
                  size="medium"
                  isDisableTyping
                />
              :
                <Fragment>
                  <div
                    className="line"
                    ref={node => this.sliderStart = node}
                  />
                  <div
                    className="line-green"
                    style={{ width: `${sliderWidth}%` }}
                  />
                  <div
                    className="knob-container"
                  >
                    {this.renderKnobs()}
                  </div>
                  <div
                    className="knob-container"
                  >
                    <button
                      tabIndex={0}
                      className={`slider-knob ${knobColor1}`}
                      ref={node => this.slider = node}
                      onMouseDown={this.handleSliderDown}
                      onClick={e => e.preventDefault()}
                      style={{ left: `${sliderWidth}%` }}
                    >
                      <div className={`inner ${knobColor2}`} />
                    </button>
                    {
                      isProductSlider ?
                        null
                      :
                        <Field name={name} component={() => null} />
                    }
                  </div>
                </Fragment>
            }
          </div>
        </Fragment>
      </FormInputWrapper>
    );
  }
}

SliderNew.defaultProps = {
  desc: null,
  hideWidth: 0,
  prefix: null,
  suffix: null,
  extraInfo: null,
  isProductSlider: false,
  name: null,
  change: null,
  storeForm: null,
  form: null,
  maxWidth: '100%',
  errorString: null,
  formState: null,
  formStateList: null,
  displayList: null,
};

SliderNew.propTypes = {
  isProductSlider: PropTypes.bool,
  inputList: PropTypes.array.isRequired,
  name: PropTypes.string,
  change: PropTypes.func,
  desc: PropTypes.string,
  storeForm: PropTypes.object,
  hideWidth: PropTypes.number,
  form: PropTypes.string,
  prefix: PropTypes.string,
  suffix: PropTypes.string,
  extraInfo: PropTypes.object,
  maxWidth: PropTypes.string,
  errorString: PropTypes.string,
  formState: PropTypes.number,
  formStateList: PropTypes.array,
  displayList: PropTypes.array,
};

const mapStateToProps = (state, ownProps) => ({
  storeForm: (getFormValues(ownProps.form)(state)),
});

export default connect(mapStateToProps, null)(SliderNew);
